.searchBar {
    padding: 0 16px;
}

@media screen and (min-width: 768px) {
    .searchBar {
        padding: 11px 14.25px 0 20.25px;
    }
}

@media screen and (min-width: 1280px) {
    .searchBar {
        padding: 0;
    }
}

.input {
    padding: 10.5px 21px;
    background: var(--search-bar-bg);
    border: 1px solid var(--toggle-bar-bg);
    border-radius: 40px;
    height: 42px;
    width: 100%;
    margin: 110px auto 50px;
    position: relative;
    transition: box-shadow 0.3s ease-in-out;
}

.input:hover,
.input:focus {
    box-shadow: 0 8px 16px rgba(52, 152, 219, 0.5);
}

.input::placeholder {
    font-weight: var(--normal);
    font-size: 16px;
    line-height: 1.3125;
    color: var(--search-bar-text);
}

@media screen and (min-width: 768px) {
    .input {
        margin: 110px auto 75px;
    }
}

@media screen and (min-width: 1280px) {
    .input {
        margin: 0;
        width: 176px;
        height: 42px;
    }
}

.searchIcon {
    position: absolute;
    top: 119px;
    right: 32.83px;
    cursor: pointer;
}

@media screen and (min-width: 768px) {
    .searchIcon {
        top: 132px;
        right: 30.83px;
    }
}

@media screen and (min-width: 1280px) {
    .searchIcon {
        top:5px;
        right: 30.83px;
       position: relative;
    }
}